import styled from "styled-components";

export const DivExit = styled.div`
  background: #ffecee;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
`;
export const SectionExit = styled.section`
  display: flex;
  margin: 25px 70px 0 20px;
  background: #fff;
  height: 40%;
  width: 360px;
`;
export const FigureExit = styled.figure``;
export const ImagenExit = styled.img`
  height: 100%;
  width: 100%;
`;
export const SectionProceso = styled.section`
  margin: 25px 70px 0 20px;
`;
export const H1Exit = styled.h1`
  text-align: center;
  font-size: 40px;
  margin-left: 25px;
  font-weight: bold;
  color: black;
  margin-bottom: 20px;
`;

export const H4Exit = styled.h4`
  text-align: justify;
  color: #888585;
  margin: 0;
  font-size: 16px;
`;
export const ButtonAceptar = styled.button`
  background: #fe174f;
  position: fixed;
  bottom: 10px;
  margin: 25px 70px 0 20px;
  height: 60px;
  width: 360px;
  color: white;
  border-radius: 6px;
  border-color: transparent;
  font-size: 20px;
`;
