import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import flecha from "../../assets/chevron_left_FILL0_wght400_GRAD0_opsz48.png";
import {
  CartShopping,
  ContainerPayment,
  Date,
  DescriptionCart,
  DetailPizza,
  FormPayment,
  InputD,
  NamePizza,
  Price,
} from "./StylePaymentPizza";
import * as Yup from "yup";
import { useFormik } from "formik";
import axios from "axios";

const validationSchema = Yup.object().shape({
  name: Yup.string()
    .min(3, "El nombre debe tener mnimo 3 caracteres")
    .required("El nombre es obligatorio"),
  cardNumber: Yup.number()
    .min(16, "El nmero de tarjeta debe tener mnimo 16 caracteres")
    .required("El nmero de tarjeta es obligatorio"),
  expirationDate: Yup.string()
    .min(5, "La fecha de vencimiento debe tener mnimo 5 caracteres")
    .max(5, "La fecha de vencimiento debe tener mximo 5 caracteres")
    .required("La fecha de vencimiento es obligatoria"),
  cvv: Yup.number()
    .min(3, "El cvv debe tener mnimo 3 caracteres")
    .required("El cvv es obligatorio"),
  direction: Yup.string()
    .min(13, "La direccin debe tener mnimo 13 caracteres")
    .max(30, "La direccin debe tener mximo 30 caracteres")
    .required("La direccin es obligatoria"),
});

const initialValues = {
  name: "",
  cardNumber: "",
  expirationDate: "",
  cvv: "",
  direction: "",
};

const PaymentPizza = () => {
  const navigate = useNavigate();

  const onClikSearch = () => {
    navigate("/home");
  };

  const formik = useFormik({
    initialValues,
    validationSchema,
    onSubmit: async (values, { setSubmitting }) => {
      try {
        // Aqu enviamos los datos al endpoint JSON utilizando axios
        await axios.post(" http://localhost:3000/Cash", values);

        // Redirigimos al usuario a la pgina de confirmacin
        navigate("/exitpayment");
      } catch (error) {
        console.error(error);
      } finally {
        setSubmitting(false);
      }
    },
  });

  const location = useLocation();
  const { cantidad, pizzaEscogida, imagen, totalPagar } = location.state;

  return (
    <ContainerPayment>
      <CartShopping>
        <button onClick={onClikSearch}>
          <img src={flecha} alt="" />
        </button>
        <h3>Carrito de compras</h3>
      </CartShopping>
      <DetailPizza>
        <div>
          <img src={imagen} alt={pizzaEscogida} />
        </div>
        <DescriptionCart>
          <NamePizza>{pizzaEscogida}</NamePizza>
          <Price>
            <h3>x {cantidad}</h3>
            <h3>$ {totalPagar}</h3>
          </Price>
        </DescriptionCart>
      </DetailPizza>
      <FormPayment>
        <h3>Informacin de pago</h3>
        <form onSubmit={formik.handleSubmit}>
          <label htmlFor="">Nombre completo</label>
          <input
            type="text"
            name=""
            id=""
            placeholder="Ingresa tu nombre"
            {...formik.getFieldProps("name")}
          />
          {formik.touched.name && formik.errors.name && (
            <div>{formik.errors.name}</div>
          )}
          <label htmlFor="">Nmero de Tarjeta de Crdito</label>
          <input
            type="text"
            placeholder="1234 1234 1234 1234"
            {...formik.getFieldProps("cardNumber")}
          />
          {formik.touched.cardNumber && formik.errors.cardNumber && (
            <div>{formik.errors.cardNumber}</div>
          )}
          <Date>
            <div>
              <label htmlFor="">Fecha de vencimiento</label>
              <InputD
                type="text"
                placeholder="12/29"
                {...formik.getFieldProps("expirationDate")}
              />
              {formik.touched.expirationDate &&
                formik.errors.expirationDate && (
                  <div>{formik.errors.expirationDate}</div>
                )}
            </div>
            <div>
              <label htmlFor="">CVV</label>
              <InputD
                type="text"
                placeholder="333"
                {...formik.getFieldProps("cvv")}
              />
              {formik.touched.cvv && formik.errors.cvv && (
                <div>{formik.errors.cvv}</div>
              )}
            </div>
          </Date>
          <label htmlFor="">Direccin</label>
          <input
            type="text"
            placeholder="Av.morelos #123"
            {...formik.getFieldProps("direction")}
          />
          {formik.touched.direction && formik.errors.direction && (
            <div>{formik.errors.direction}</div>
          )}
          <button type="submit">Pagar Ahora</button>
        </form>
      </FormPayment>
    </ContainerPayment>
  );
};

export default PaymentPizza;
