import React, { useEffect, useState, useContext } from "react";
import {
  Background,
  Content,
  H1,
  H2,
  Image,
  P,
  Section,
  ARestablecer,
  PCuenta,
  ARegistrate,
  Message,
  ButtonLogin,
} from "./StyleLogin";
import icono from "../../assets/iconoPizza.png";
import { InputText } from "../component/InputText";
import user from "../../assets/user.svg";
import closed from "../../assets/lock-closed.svg";
import * as Yup from "yup";
import { useFormik } from "formik";
import { Link, useNavigate } from "react-router-dom";
import { get } from "../../service/apiPizza";
import Swal from "sweetalert2";
import { UserContext } from "../context/UserContext";

const validationSchema = Yup.object().shape({
  user: Yup.string()
    .min(3, "El nombre de usuario debe tener mnimo 3 caracteres")
    .max(15, "El nombre de usuario debe tener mximo 15 caracteres")
    .required("El nombre de usuario es obligatorio"),
  password: Yup.number()
    .min(3, "La contrasea debe tener mnimo 3 caracteres")
    .required("La contrasea es obligatoria"),
});

const initialValues = {
  user: "",
  password: "",
};

const Login = () => {
  const [links, setLinks] = useState([]);
  const { setUser } = useContext(UserContext);

  const getUsers = async () => {
    const response = await get("users");
    console.log(response);
    setLinks(response);
  };

  useEffect(() => {
    getUsers();
  }, []);

  const navigate = useNavigate();

  const formik = useFormik({
    initialValues,
    validationSchema,
    onSubmit: (values, { setSubmitting, setFieldError }) => {
      try {
        // Validar si el usuario y la contrasea existen en la data
        const userExists = links.find(
          (link) =>
            link.user === values.user && link.password === values.password
        );

        if (userExists) {
          Swal.fire({
            position: "center",
            icon: "success",
            title: "Inicio de sesion exitoso",
            showConfirmButton: false,
            timer: 1500,
          });
          // Usuario vlido, redirigir a la siguiente pgina con los datos del usuario
          navigate("/home", { state: { user: userExists } });

          // Establecer los datos del usuario en el contexto
          setUser(userExists);

          // Usuario vlido, redirigir a la siguiente pgina
          navigate("/home");
        } else {
          // Usuario no vlido, mostrar mensaje de error
          setFieldError("user", "Usuario o contrasea incorrectos");
        }
      } catch (error) {
        console.error(error);
      }

      setSubmitting(false);
    },
  });

  return (
    <>
      <Background />
      <Content>
        <Section>
          <Image src={icono} alt="icono" />
        </Section>
        <H1>piSassScript</H1>
        <H2>Inicia sesin en tu cuenta</H2>
        <P>
          Disfruta de la mejor Pizza creada para las personas amantes del cdigo
        </P>
        <form onSubmit={formik.handleSubmit}>
          <InputText
            type="text"
            iconSrc={user}
            placeholder={"Usuario"}
            {...formik.getFieldProps("user")}
          />
          {formik.touched.user && formik.errors.user && (
            <Message>{formik.errors.user}</Message>
          )}
          <InputText
            type="password"
            iconSrc={closed}
            placeholder={"Contrasea"}
            {...formik.getFieldProps("password")}
          />
          {formik.touched.password && formik.errors.password && (
            <Message>{formik.errors.password}</Message>
          )}
          <ButtonLogin type="submit" disabled={formik.isSubmitting}>
            Iniciar sesin
          </ButtonLogin>
        </form>

        <ARestablecer href="#">Restablecer contrasea</ARestablecer>
        <PCuenta>No tienes una cuenta?</PCuenta>
        <Link to={"/newRecord"}>
          <ARegistrate href="#">Regstrate aqu</ARegistrate>
        </Link>
      </Content>
    </>
  );
};

export default Login;
